                <p>Access to your dashboard</p>
              </el-col>
              <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
                <el-form-item label="Email Address" prop="email" :rules="[
                  {
                    required: true,
                    message: 'Email address is required',
                    trigger: ['blur'],
                  },
                  {
                    type: 'email',
