<script>
export default {
  data() {
    return {
      loading: false,
      message: "",
      user: {},
      authenticated: true,
    };
  },
  computed: {
    loggedIn() {
      return this.$store.state.auth.status.loggedIn;
    },
  },
  created() {
    if (this.loggedIn) {
      this.$router.push("/");
    }
  },
  methods: {
    handleLogin(validate) {
      this.$refs[validate].validate((valid) => {
        if (valid) {
          this.loading = true;
          let data = this.user;
          this.$store.dispatch("auth/login", data).then(
            (res) => {
              this.loading = false;
              this.user = {};
              this.$notify({
                title: "Hi " + this.$store.state.auth.user.name,
                message:
                  "Welcome to Techsavanna! We are thrilled to have you trust our services.",
                type: "success",
              });
              this.$router.push("/");
              // if (res.auth_code) {
              //   this.authenticated = false;
              //   this.$notify({
              //     title: "Verification",
              //     message:
              //       "Hi " +
              //       this.$store.state.auth.user.name +
              //       ", Please check your email! We've sent you an email with a verification code to make sure it's really you signing in. The code will expire in an hour.",
              //     type: "warning",
              //   });
              //   this.$store.dispatch("auth/logout");
              // } else if (res.login_attempts == 10 && !res.auth_code) {
              //   this.$notify({
              //     title: "Info",
              //     message: "Change your first time login password",
              //     type: "info",
              //   });
              //   this.$router.push("/settings/password");
              // } else {
              //   this.user = {};
              //   this.$notify({
              //     title: "Hi " + this.$store.state.auth.user.name,
              //     message:
              //       "Welcome to Techsavanna! We are thrilled to have you trust our services.",
              //     type: "success",
              //   });
              //   this.$router.push("/");
              // }
            },
            (error) => {
              this.loading = false;
              this.message =
                (error.response &&
                  error.response.data &&
                  error.response.data.message) ||
                error.message ||
                error.toString();
              this.$notify({
                title: "Error",
                message: this.message,
                type: "error",
              });
            }
          );
        } else {
          return false;
        }
      });
    },
  },
};
</script>